<template>

    <div class="row">
        <b-modal :id="formId" :size="formWidth">
            <template #modal-title >
                <div v-if="formState=='Update'">Update Menus #{{formData.id}}</div>
                <div v-if="formState=='Create'">Create Menus </div>
            </template>

            <EditMenus
                v-if="formState=='Update'"
            :modalFormId="formId"
            :key="formKey"
            :data="formData"
            :gridApi="formGridApi"
            @close="closeForm"
                            :entreprisesData="entreprisesData"
                            :menusData="menusData"
                        />


            <CreateMenus
                v-if="formState=='Create'"
            :modalFormId="formId"
            :key="formKey"
            :gridApi="formGridApi"
            @close="closeForm"
                            :entreprisesData="entreprisesData"
                            :menusData="menusData"
                        />

            <template #modal-footer >
                <div ></div>
            </template>
        </b-modal>



        <div class="col-sm-12">
            <AgGridTable
                :key="tableKey"
                domLayout='autoHeight'
                rowSelection="multiple"
                className="ag-theme-alpine"
                :columnDefs="columnDefs"
                :url="url"
                :rowModelType="rowModelType"
                :paginationPageSize="paginationPageSize"
                :cacheBlockSize="cacheBlockSize"
                :maxBlocksInCache="maxBlocksInCache"
                :pagination="pagination"
                :rowData="rowData"
                @gridReady="onGridReady"

            >
                <template #header_buttons>
                    <div class="btn btn-primary"  v-if="!routeData.meta.hideCreate" @click="openCreate"><i class="fa fa-plus"></i> Nouveau</div>
                </template>

            </AgGridTable>

        </div>
    </div>
</template>



<script>
    import DataTable from '@/components/DataTable.vue'
    import AgGridTable from "@/components/AgGridTable.vue"
    import CreateMenus from './CreateMenus.vue'
    import EditMenus from './EditMenus.vue'
    import CustomSelect from "@/components/CustomSelect.vue"
    import CustomFiltre from "@/components/CustomFiltre.vue"
    import DataModal from '@/components/DataModal.vue'
    import moment from 'moment'

    import AgGridBtnClicked from "@/components/AgGridBtnClicked.vue";

    export default {
        name: 'MenusView',
        components: {  DataTable,AgGridTable, CreateMenus, EditMenus, DataModal,AgGridBtnClicked,CustomSelect,CustomFiltre },
        data () {

            return {
                formId:"menus",
                formState:"",
                formData:{},
                formWidth:'lg',
                formGridApi:{},
                formKey:0,
                tableKey:0,
                url: 'http://127.0.0.1:8000/api/menus-Aggrid',
                table: 'menus',
                                        entreprisesData:[],
                                        menusData:[],
                                requette: 2,
                columnDefs: null,
                rowData: null,
                gridApi: null,
                columnApi: null,
                rowModelType: null,
                pagination: true,
                paginationPageSize: 100,
                cacheBlockSize: 10,
                maxBlocksInCache: 1,
            }
        },

        computed:{
            routeData:function () {
                let router={meta:{}}
                if(window.router){
                    try{ router=window.router; }catch (e) { }
                }


                return router
            },
            taille:function(){
                let result='col-sm-12'
                if(this.filtre){
                    result='col-sm-9'
                }
                return result
            },
        },
        watch:{
            'routeData': {
                handler: function (after, before) {
                    this.gridApi.setFilterModel(null)
                    this.gridApi.refreshServerSide()
                },
                deep: true
            },
        },
        created() {
            this.url= this.axios.defaults.baseURL+'/api/menus-Aggrid',
                this.formId=this.table+"_"+Date.now()
            this.rowBuffer = 0;
            this.rowModelType = 'serverSide';
            this.cacheBlockSize = 50;
            this.maxBlocksInCache = 2;

        },
        beforeMount() {
            this.columnDefs =
                [
                    {
                        field: null,
                        headerName: '',
                        suppressCellSelection: true,
                        width: 80,
                        pinned: 'left',
                        cellRendererSelector: params => {
                            return {
                                component: 'AgGridBtnClicked',
                                params:{
                                    clicked: field=> {
                                        this.showForm('Update',field,params.api)
                                    },
                                    render:`<div class="btn btn-primary" style="width:100%;height:100%;color:#fff;border-raduis:5px;text-align:center;cursor:pointer">  <i class="fa-solid fa-pen-to-square "></i></div>`
                                }
                            };
                        },

                    },
                                                
                                                
                                            {
                        field: "name",
                        sortable: true,
                        filter: 'agTextColumnFilter',filterParams: {suppressAndOrCondition: true,},
                        headerName: 'name',
                    },
                        


                        
                                                
                                            {
                        field: "icon",
                        sortable: true,
                        filter: 'agTextColumnFilter',filterParams: {suppressAndOrCondition: true,},
                        headerName: 'icon',
                    },
                        


                        
                                                
                                            {
                        field: "slug",
                        sortable: true,
                        filter: 'agTextColumnFilter',filterParams: {suppressAndOrCondition: true,},
                        headerName: 'slug',
                    },
                        


                        
                                                
                                            {
                        field: "url",
                        sortable: true,
                        filter: 'agTextColumnFilter',filterParams: {suppressAndOrCondition: true,},
                        headerName: 'url',
                    },
                        


                        
                                                
                                            {
                        field: "ordre",
                        sortable: true,
                        filter: 'agTextColumnFilter',filterParams: {suppressAndOrCondition: true,},
                        headerName: 'ordre',
                    },
                        


                        
                                                
                                            {
                        field: "isSu",
                        sortable: true,
                        filter: 'agTextColumnFilter',filterParams: {suppressAndOrCondition: true,},
                        headerName: 'isSu',
                    },
                        


                        
                                                
                                                
                                                
                                            {
                        field: "creat_by",
                        sortable: true,
                        filter: 'agTextColumnFilter',filterParams: {suppressAndOrCondition: true,},
                        headerName: 'creat_by',
                    },
                        


                        
                                                
                                                
                                                
                                                
                                                
                                            {
                        field: "identifiants_sadge",
                        sortable: true,
                        filter: 'agTextColumnFilter',filterParams: {suppressAndOrCondition: true,},
                        headerName: 'identifiants_sadge',
                    },
                        


                        
                                                
                        
                        
                        
                        
                        
                    {
                        headerName: 'entreprise',
                        field: 'entreprise.Selectlabel',
                    },
                    {

                        hide: true,
                        suppressColumnsToolPanel: true,

                        headerName: 'entreprise',
                        field: 'entreprise_id',
                        valueFormatter: params => {
                            let retour=''
                            try{
                                return params.data['entreprise']['Selectlabel']
                            }catch (e) {

                            }
                            return  retour
                        },

                        filter: "CustomFiltre",
                        filterParams: {
                            url: this.axios.defaults.baseURL + '/api/entreprises-Aggrid',
                            columnDefs: [
                                {
                                    field: "",
                                    sortable: true,
                                    filter: "agTextColumnFilter",
                                    filterParams: { suppressAndOrCondition: true },
                                    headerName: "",
                                    cellStyle: { fontSize: '11px' },
                                    valueFormatter: (params) => {
                                        let retour = "";
                                        try {
                                            return `${params.data["Selectlabel"]}`;
                                        } catch (e) {
                                        }
                                        return retour;
                                    },
                                },
                            ],
                            filterFields: ['libelle'],
                        },
                    },

                    
                        
                        
                        
                        
                        
                    {
                        headerName: 'menu',
                        field: 'menu.Selectlabel',
                    },
                    {

                        hide: true,
                        suppressColumnsToolPanel: true,

                        headerName: 'menu',
                        field: 'menu_id',
                        valueFormatter: params => {
                            let retour=''
                            try{
                                return params.data['menu']['Selectlabel']
                            }catch (e) {

                            }
                            return  retour
                        },

                        filter: "CustomFiltre",
                        filterParams: {
                            url: this.axios.defaults.baseURL + '/api/menus-Aggrid',
                            columnDefs: [
                                {
                                    field: "",
                                    sortable: true,
                                    filter: "agTextColumnFilter",
                                    filterParams: { suppressAndOrCondition: true },
                                    headerName: "",
                                    cellStyle: { fontSize: '11px' },
                                    valueFormatter: (params) => {
                                        let retour = "";
                                        try {
                                            return `${params.data["Selectlabel"]}`;
                                        } catch (e) {
                                        }
                                        return retour;
                                    },
                                },
                            ],
                            filterFields: ['libelle'],
                        },
                    },

                                    ];


        },
        mounted () {
            if(this.requette>0){
                // this.$store.commit('setIsLoading', true)
            }

                            this.getentreprises();
                            this.getmenus();
            
        },
        methods: {
            closeForm(){this.tableKey++},
            openCreate(){
                this.showForm('Create',{},this.gridApi)
            },
            showForm(type,data,gridApi,width='lg'){
                this.formKey++
                this.formWidth=width
                this.formState=type
                this.formData=data
                this.formGridApi=gridApi
                this.$bvModal.show(this.formId)
            },
            onGridReady(params) {
                console.log('on demarre',params)
                this.gridApi = params.api;
                this.columnApi = params.columnApi;
                this.isLoading = false
            },
                        getentreprises () {
                this.axios.get('/api/entreprises').then((response) => {
                    this.requette--
                    if(this.requette==0){
                        // this.$store.commit('setIsLoading', false)
                    }
                    this.entreprisesData = response.data

                }).catch(error => {
                    console.log(error.response.data)
                    // this.$store.commit('setIsLoading', false)
                    this.$toast.error('Erreur survenue lors de la récuperation')
                })
            },

                        getmenus () {
                this.axios.get('/api/menus').then((response) => {
                    this.requette--
                    if(this.requette==0){
                        // this.$store.commit('setIsLoading', false)
                    }
                    this.menusData = response.data

                }).catch(error => {
                    console.log(error.response.data)
                    // this.$store.commit('setIsLoading', false)
                    this.$toast.error('Erreur survenue lors de la récuperation')
                })
            },

                    }
    }
</script>
